@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
:root {
  --main-primary-color: #4884f8;
  --main-black-600: #393939;
  --main-black-500: #6d6d6d;
  --main-black-400: #9a9a9a;
  --main-black-300: #cdcdcd;
  --main-black-200: #f2f2f2;
  --main-black-100: #fbfbfb;
  --main-green: #2eba77;
  --main-red: #df6161;
  --main-lightred: #f5c3c3;
  --main-lightyellow: #faf5e3;
  --main-blue-600: #0f4d92;
  --main-blue-200: #c3dbf5;
  --main-lightpurple: #e7dff0;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: 'Poppins', sans-serif !important;
  color: #393939 !important;
  color: var(--main-black-600) !important;
  background-color: #f2f2f2 !important;
  background-color: var(--main-black-200) !important;
}

.primary {
  color: #4884f8;
  color: var(--main-primary-color);
}

.green {
  color: #2eba77;
  color: var(--main-green);
}

.green-bg {
  background-color: #2eba77;
  background-color: var(--main-green);
}

.red {
  color: #df6161;
  color: var(--main-red);
}

.red-bg {
  background-color: #df6161;
  background-color: var(--main-red);
}

.black-500 {
  color: #6d6d6d;
  color: var(--main-black-500);
}
.black-500-bg {
  background-color: #6d6d6d;
  background-color: var(--main-black-500);
}

.black-400 {
  color: #9a9a9a;
  color: var(--main-black-400);
}

.black-400-bg {
  background-color: #9a9a9a;
  background-color: var(--main-black-400);
}

.black-300 {
  color: #cdcdcd;
  color: var(--main-black-300);
}

.black-300-bg {
  background-color: #cdcdcd;
  background-color: var(--main-black-300);
}

.black-200 {
  color: #f2f2f2;
  color: var(--main-black-200);
}

.black-200-bg {
  background-color: #f2f2f2;
  background-color: var(--main-black-200);
}

.blue-600 {
  color: #0f4d92;
  color: var(--main-blue-600);
}

.blue-600-bg {
  background-color: #0f4d92;
  background-color: var(--main-blue-600);
}

.blue-200 {
  color: #c3dbf5;
  color: var(--main-blue-200);
}

.blue-200-bg {
  background-color: #c3dbf5;
  background-color: var(--main-blue-200);
}

.lightyellow {
  color: #faf5e3;
  color: var(--main-lightyellow);
}

.lightyellow-bg {
  background-color: #faf5e3;
  background-color: var(--main-lightyellow);
}

.lightred {
  color: #f5c3c3;
  color: var(--main-lightred);
}

.lightred-bg {
  background-color: #f5c3c3;
  background-color: var(--main-lightred);
}

.lightpurple {
  color: #e7dff0;
  color: var(--main-lightpurple);
}

.lightpurple-bg {
  background-color: #e7dff0;
  background-color: var(--main-lightpurple);
}

.small-image {
  height: 63px;
  width: 63px;
}

.large-image {
  height: 136px;
  width: 136px;
}

.p1 {
  font-size: 40px;
}

.p2 {
  font-size: 32px;
}

.p3 {
  font-size: 24px;
}

.p4 {
  font-size: 18px;
}

.p5 {
  font-size: 16px;
}

.p6 {
  font-size: 14px;
}
.p7 {
  font-size: 12px;
}

.font-weight-semibold {
  font-weight: 600;
}

.font-weight-medium {
  font-weight: 500;
}

@media (min-width: 768px) {
  .p1 {
    font-size: 48px;
  }

  .p2 {
    font-size: 36px;
  }

  .p3 {
    font-size: 24px;
  }

  .p4 {
    font-size: 18px;
  }

  .p5 {
    font-size: 16px;
  }

  .p6 {
    font-size: 14px;
  }
}

/* Override Bootstrap Styles */
.form-check-label {
  color: #9a9a9a;
  color: var(--main-black-400);
}

.form-control:focus {
  border: 2px solid #4884f8 !important;
  border: 2px solid var(--main-primary-color) !important;
  box-shadow: none;
}

.upload-file-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 2em;
}

.upload-file-container {
  min-height: 700px;
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 75%;
  margin-top: 3em;
  align-items: center;
  padding-bottom: 3em;
}

.upload-file-step-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.volunteer-card:hover {
  cursor: pointer;
}

.volunteer-card-active {
  border-right: 3px solid #0089ff;
}

.contact-card {
  width: 225px;
}

.update-link {
  width: -webkit-fit-content;
  width: fit-content;
  width: -moz-fit-content;
  position: absolute;
  top: 1em;
  right: 0.5em;
}

.cancel-update {
  width: -webkit-fit-content;
  width: fit-content;
  width: -moz-fit-content;
  position: absolute;
  top: 1em;
  left: 0.5em;
}

.confirm-remove {
  margin-left: 2em;
  margin-right: 2em;
  margin-bottom: 1em;
}

.volunteer-sidebar {
  width: 22em;
  position: relative;
}

.letter-card-preview {
  max-height: 50px;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-top: 1em;
  margin-bottom: 1em;
}

.letter-card:hover {
  cursor: pointer;
}

.volunteers-list-sidebar {
  width: 20em;
}

.letter-category {
  width: 50%;
  padding-top: 1em;
}

#volunteers-spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  opacity: 100%;
  z-index: 100;
}

#problem-loading-info {
  position: relative;
  margin-top: 5em;
}

#faded {
  opacity: 50%;
}

.hidden {
  display: none;
}

.copy-share-link:hover {
  cursor: pointer;
}

.logo {
  cursor: pointer;
  width: 15%;
  height: 15%;
  margin-top: -20px;
}

.navbar {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 10px 0px;
}

.nav-link {
  margin-top: 20px;
  padding: 0.5em 0;
}

#login-form {
  padding-top: 4em;
}

#login-spinner {
  position: fixed;
  top: 50%;
  left: 50%;
}

.docdrop {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2;
  border-radius: 2;
  border-color: #eeeeee;
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border 0.24s ease-in-out;
}

.docdrop:hover {
  cursor: pointer;
}
.docdrop-active {
  border-color: #2196f3;
}

.tag-row:hover {
  color: #212529;
  background-color: rgba(0, 0, 0, 0.075);
}

.tag-selector-container {
  max-height: 300px;
}

.tag-selector-input {
  /* background-color: ; */
  border: 0px;
}

.tag-selector-input:focus {
  outline: none;
}

#contacts-spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  opacity: 100%;
  z-index: 100;
}

#faded {
  opacity: 50%;
}

